:root {
  --background-color: #278ed3;
  --hover-color: #52b788;
  --nav-text-color: white;
  --dark-text-color: #696969;
  --sub-text-color: #999999;
}



.requestform {
  width: 100vw;
}

form {
  display: flex;
  justify-content: center;
  margin: 90px;
}

.formContent {
  display: flex;
  flex-direction: column;
  flex-wrap: nowrap;
  margin: 0 10px;
  align-items: center;
}



input {
  display: inline-block;
  border-radius: 5px;
  border: 1px solid silver;
  margin-bottom: 25px;
  width: 250px;
  height: 50px;
  padding: 10px 18px;
  font-size: 16px;
  color: #696969;
  color: var(--dark-text-color);
  transition: 0.3s;
  outline: none;
}

input:focus,
input:hover {
  border: 1px solid #696969;
  border: 1px solid var(--dark-text-color);
  box-shadow: 6px 6px #696969;
  box-shadow: 6px 6px var(--dark-text-color);
}

input:hover::-webkit-input-placeholder{
  color:#696969;
  color:var(--dark-text-color);
  -webkit-transition: .3s;
  transition: .3s;
}

input:hover::placeholder{
  color:#696969;
  color:var(--dark-text-color);
  transition: .3s;
}
input::-webkit-input-placeholder{
  color: silver;
  -webkit-transition: .3s;
  transition: .3s;
}
input::placeholder{
  color: silver;
  transition: .3s;
}

.errorMessage {
  color:  #c90e0e;
 width: 100%;
 padding: 7px;
 padding-left: 18px;
  background-color: #fccfcc;
  margin-bottom: 15px;
  font-weight: 500;
  font-size: 15px;
}

.subErrorMessage{
  padding: 0;
  margin: 0;
  width: 100%;
  margin-bottom: 15px;
  font-size: 14px;
  color: #CC3333;
  font-weight:  700;
  
}
/*Button Section--------------------*/
input[type="submit"],
input[type="button"] {
  border-radius: 25px;
  background-color: #278ed3;
  background-color: var(--background-color);
  color: white;
  font-size: 17px;
  font-weight: 600;
  height: 45px;
  margin: 0;
  margin-top: 10px;
  cursor: pointer;
  transition: 0.3s;
  border-color: #278ed3;
  border-color: var(--background-color);
  letter-spacing: 1px;
}

input[type="submit"]:hover,
input[type="button"]:hover {
  transition: 0.2s;
  box-shadow: none;
  background-color: #52b788;
  background-color: var(--hover-color);
  border-color: #52b788;
  border-color: var(--hover-color);
}

input.signUp{
  background-color: white;
  color: #278ed3;
  color: var(--background-color);
}
input.signUp:hover {
  color:#52b788;
  color:var(--hover-color);
  border-color: #52b788;
  border-color: var(--hover-color);
  background-color: transparent;
}


button {
  font-size: 15px;
  letter-spacing: 1px;
  height: 40px;
  width: 90px;
  margin-left: 15px;
  border-radius: 25px;
  border: 2px solid #278ed3;
  border: 2px solid var(--background-color);
  background-color: #278ed3;
  background-color: var(--background-color);
  color: white;
  color: var(--nav-text-color);
  font-weight: 600;
  cursor: pointer;
  transition: 0.3s;
}

button:hover {
  border-color: #278ed3;
  border-color: var(--background-color);
  background-color: white;
  background-color: var(--nav-text-color);
  color: #278ed3;
  color: var(--background-color);
  transition: 0.2s;
  box-shadow: none;
}

input[type="submit"]:focus,
input[type="button"]:focus {
  box-shadow: none;
}

.deleteIcon {
  pointer-events: auto;
  width: 10px;
}
.deleteIcon:hover {
  cursor: pointer;
}


/*Text Section------------------------*/
h2 {
  text-align: center;
  font-weight: 900;
  font-size: 30px;
  margin-bottom: 40px;
  color: #696969;
  color: var(--dark-text-color);
}

h3 {
  margin-bottom: 15px;
  color: #696969;
  color: var(--dark-text-color);
}

.formContent ul {
  list-style: none;
  width: 280px;
  height: 210px;
  overflow: auto;
  padding-right: 30px;
}

.formContent li {
  border: 1px solid silver;
  border-radius: 50px;
  height: 40px;
  display: flex;
  justify-content: space-between;
  padding: 12px 18px 12px 0;
  margin-bottom: 10px;
  pointer-events: none;
}

.listitem {
  align-self: center;
  font-size: 14px;
  color: #403d39;
  margin-left: 18px;
  font-weight: 400;
  width: 80%;
  overflow: hidden;
  pointer-events: none;
}

/*list animation*---------------------------*/
.userInFormList-exit {
  margin-left: 0;
  transition: ease-out 0.5s;
  overflow: hidden;
}
.userInFormList-exit-active {
  margin-left: 80%;
  transition: ease-out 0.5s;
}

.userInFormList-enter {
  opacity: 1;
  transition: 0.4s;
  width: 0;
}
.userInFormList-enter-active {
  opacity: 1;
  transition: 0.4s;
  width: 100%;
}

/* scoll bar */
/* width */
ul::-webkit-scrollbar, div::-webkit-scrollbar {
  width: 7px;
  border-radius: 20px;
  -webkit-transition: 0.2s;
  transition: 0.2s;
}
/* Track */
::-webkit-scrollbar-track {
  background: rgb(201, 240, 255,.4);
  -webkit-transition: 0.2s;
  transition: 0.2s;
}

/* Handle */
::-webkit-scrollbar-thumb {
  background: rgb(158, 216, 255,.7);
  border-radius: 10px;
  -webkit-transition: 0.2s;
  transition: 0.2s;
}

/* Handle on hover */
::-webkit-scrollbar-thumb:hover {
  background: #278ed3;
  background: var(--background-color);
  -webkit-transition: 0.5s;
  transition: 0.5s;
}

/*placeholder settings*/
::-webkit-input-placeholder {
  /* Chrome, Firefox, Opera, Safari 10.1+ */
  color: silver;
  opacity: 1; /* Firefox */
}
::placeholder {
  /* Chrome, Firefox, Opera, Safari 10.1+ */
  color: silver;
  opacity: 1; /* Firefox */
}

:-ms-input-placeholder {
  /* Internet Explorer 10-11 */
  color: silver;
}

::-ms-input-placeholder {
  /* Microsoft Edge */
  color: silver;
}

/*Remove all the arrows from input*/
/* Chrome, Safari, Edge, Opera */
input::-webkit-outer-spin-button,
input::-webkit-inner-spin-button {
  -webkit-appearance: none;
  margin: 0;
}

/* Firefox */
input[type=number] {
  -moz-appearance: textfield;
}

/*dashboard form setting*/
.dashboard form{
  margin: 0;
  padding: 0;
  flex-direction: column;
  padding-top: 1px;

}

.dashboard .pay-button{
  margin-top: 25px;
  height: 40px;
}


.dashboard{
  display: grid;
  grid-template-columns: repeat(5, 1fr);
  grid-auto-rows: 200px 50vh;
  grid-gap: 15px;
  gap: 15px;
  justify-content: center;
  align-content: center;
  padding: 25px 5vw;
  min-width: 1100px;
  max-width: 1300px;
  min-height: 550px;
  max-height: 100vh;
  margin: auto;
}

.dashboard h2{
  margin: 0;
  text-align: left;
}

.dashboard .request-container h3{
  text-align: center;
}
/*components*/
.placeholder {
  width: auto;
  box-shadow: 0 0 5px var(--background-color);
  border-radius: 25px;
  padding: 35px;
}

.dashboard .info-container{
  grid-column: 1/4;
}
.history-container{
  grid-column: 1 / 4;
}
.request-container{
grid-column: 4/6;
grid-row: 1/3;
display: flex;
flex-direction: column;
align-content: center;
}

.dashboard th, .dashboard td{
  text-align: left;
  width:auto;
  padding: 10px;
  font-size: 15px;
  font-weight: 400;
}

.dashboard tr{
opacity: .6;
transition: .3s;
}
.dashboard tr:hover{
  background-color: rgba(215, 239, 255, 0.4);
  opacity: 1;
}
.dashboard .history-table{
  height: 90%;
  overflow: auto;
}

.dashboard a{
  display: flex;
  align-items: center;
  justify-content: center;
  padding: 10px;
}

.dashboard a:visited{
color: var(--dark-text-color);
}

.dashboard button{
  font-size: 13px;
  font-weight: 600;
  width: auto;
  height: auto;
  padding: 6px 15px;
  margin: 0;
}


/*Request Option Button Section*/
.dashboard .option1{
  border-radius: 25px 0 0 25px;
  background: linear-gradient(to right, transparent 50%, #278ed3 50%);
  background-size: 200%;
  background-position: left;
  color: var(--background-color);
  width: 100px;
}


.dashboard .option1:hover{
  background-position: right;
  color: white;
}

.dashboard .option2{
  border-radius: 0 25px 25px 0;
  background: linear-gradient(to left, transparent 50%, #278ed3 50%);
  background-size: 200%;
  background-position: right;
  color: var(--background-color);
  width: 100px;
}

.dashboard .option2:hover{
  background-position: left;
  color: white;
}


/*End of request button*/

/*each item in the request data*/
.dashboard .items{
  height: 90%;
  overflow-Y: auto;
  scroll-behavior: smooth;
  padding-right: 15px;
  margin-right: 0px;
}
.dashboard .item{
  border: 1px solid silver;
  margin-bottom: 25px;
  border-radius: 25px;
  padding: 20px;
  display: grid;
  grid-template-rows: repeat(5, 1fr);
  grid-template-rows: auto;
  grid-row-gap: 5px;
  row-gap: 5px;

}

.item .item-1{
grid-column: 1/4;
grid-row: 1/2;
font-size: large;
color: var(--dark-text-color);
font-weight: 900;
overflow: hidden;
}
.item .item-2{
  grid-column: 4/6; 
  text-align: right; 
  grid-row: 1/2;
  color: var(--sub-text-color);
  font-weight: 500;
  font-size: 15px;
  overflow: hidden;
}
.item .item-3{
  grid-column: 1/4;
  grid-row: 3/4;
  margin-top: 20px;
  font-weight: 600;
  display: flex;
  align-items: flex-end;
  color: var(--dark-text-color);

}
.item .item-4{
  grid-column: 1/4; 
  grid-row: 2/3;
  font-size: small;
  font-weight: 500;
  color: var(--sub-text-color);
}
.item .item-5{
  grid-column: 5/6;
  grid-row: 3/4;
  margin-top: 20px;
}

.item .item-6{
  grid-column: 4/6; 
  text-align: right; 
  grid-row: 2/3;
  color: var(--sub-text-color);
  font-weight: 500;
  font-size: 15px;
  overflow: hidden;
}

/*info section*/
.userinfo{
  display: grid;
  grid-template-columns: repeat(5,1fr);
  grid-template-rows: repeat(2,1fr);
  grid-gap:15px;
  gap:15px;
  height: 100%;
}
.userinfo .placeholder{
  padding: 15px;
  padding-top: 10px;
}
.userinfo .username{
  grid-column: 1/4;
  grid-row: 1/3;
  padding: 35px;
  padding-top: 25px;
}

.userinfo .receive{
  grid-column: 4/6;
  grid-row: 1/2;
}

.userinfo .sent{
  grid-column: 4/6;
  grid-row: 2/3;
}



.userinfo h3{
  text-align: center;
  margin-top: 10px;
}

.userinfo p{
  font-size: 15px;
  font-weight: 500;
  color: var(--sub-text-color);
}
/*invoice*/
.invoice{
  display: flex;
  flex-direction: column;
  width: auto;
}

.invoice strong{
  margin-top: 25px;
  padding: 0;
}

.invoice p{
  color:var(--sub-text-color);
  margin-top: 2px;
}

.invoice strong{
  font-size: 16px;
}

strong{
color:var(--dark-text-color)
}


.overlay {
    visibility: hidden;
    opacity: 0;
    position: fixed;
    top: 0;
    bottom: 0;
    left: 0;
    right: 0;
    background: rgba(0, 0, 0, 0.6);
    transition: opacity .2s;
}

.popup {
    margin: 25px auto;
    padding: 50px;
    background: white;
    border-radius: 5px;
    width: 500px;
    height: 90vh;
    max-height: 650px;
    position: relative;
    transition: all .2s ease-in-out;
}

    .popup h2 {
        color: var(--dark-text-color);
        text-align: left;
        font-size: 25px;
        padding-left: 10px;
        padding-top: 10px;
    }

    .popup .close {
        position: absolute;
        top: 20px;
        right: 30px;
        transition: all 400ms;
        font-size: 32px;
        width:45px;
        height: 45px;
        text-align: center;
        font-weight: bold;
        text-decoration: none;
        color: var(--dark-text-color);
    }

        .popup .close:hover {
            cursor: pointer;
            color: var(--dark-text-color);
            -webkit-transform: rotate(180deg);
                    transform: rotate(180deg);
        }

    .popup .content {
        height: 90%;
        overflow: auto;
        display: flex;
        flex-direction: column;
        align-items: center;
        padding: 0;
        margin-top: 30px;

    }


    .dashboard .popup{
        width: 500px;
        padding: 10px;

    }

    .dashboard .content{
        overflow-y: auto;
        height: 85%;
    }

