:root {
  --nav-background-color: #278ed3;
  --nav-text-color: white;
}
.nav {
  display: flex;
  align-items: center;
  padding: 0px 50px;
  background-color: #278ed3;
  background-color: var(--nav-background-color);
  height: 80px;
}

.nav ul {
  display: flex;
  list-style-type: none;
  margin: 0;
  height: 100%;
  padding: 0;
  align-items: center;
  text-align: center;
  justify-content: center;
  flex: 1 1;
}

.nav li {
  list-style: none;
  display: flex;
  padding: 0;
  height: 100%;
  align-items: center;
  justify-content: center;
  
}

.nav a {
  text-decoration: none;
  display: flex;
  align-items: center;
  color: white;
  color: var(--nav-text-color);
  transition: all 0.5s ease 0s;
  padding: 20px 20px;
  margin: 0;
  font-size: 17px;
  font-weight: 400;
  width: 100%;
  height: 100%;
}


.nav a:hover {
  color: #278ed3;
  color: var(--nav-background-color);
  background-color: #b7e2ff;
  width: 100%;
}

.nav button {
  width: 100px;
  margin-left: 15px;
  font-size: 14px;
  border-color: #278ed3;
  border-color: var(--nav-background-color);
}

.nav button:hover {
    border-color: white;
    border-color: var(--nav-text-color);
    background-color: #278ed3;
    background-color: var(--nav-background-color);
    color: white;
    color: var(--nav-text-color);
}

.logo {
  font-size: 35px;
